export default {
  data() {
    return {
      currentTab: 'All',
      navBarItems: [{
        id: 'All',
        title: '全部'
      }, {
        id: 'Bags',
        title: '包配'
      }, {
        id: 'Housewares',
        title: '家居'
      }, {
        id: 'Shoes',
        title: '鞋袜'
      }, {
        id: 'Others',
        title: '其他'
      }]
    };
  },

  methods: {
    itemClick(event) {
      this.currentTab = event.target.id; // 发出toggleComp事件给父组件

      this.$emit('toggleComp', event.target.id);
    }

  }
};